$england:(brighton camborne cambridge coventry gloucester guildford henly-on-thames hersham leeds london melrose
northampton otley stockport sunbury-on-thames twickenham worcester);
$ireland:(castlereagh);
$scotland:(aberdeen edinburgh galashiels scotstoun);
$wales:(brecon cardiff colwyn-bay crosskeys ebbw-vale neath newport pontypool pontypridd whitland);

.flag-icon {
    background: {
        size: cover;
        position: 50%;
        repeat: no-repeat;
    }
    position: relative;
    display: inline-block;
    width: 1.33333333em;
    line-height: 1;
    padding: 0.15em;
    box-shadow: 0 0 1px;

    &::before {
        content: '\00a0';
    }

    &-background {
        background: {
            size: cover;
            position: 50%;
            repeat: no-repeat;
        }
        box-shadow: 0 0 1px;
        padding: 13px 17.3333px;
        margin: 0 auto;
        width: 0;
        height: 0;
    }

    &-squared {
        width: 1em;
    }

    @each $town in $england {
        &-#{unquote($town)} {
            background-image: url(#{$flag}en.svg);
        }
    }

    @each $town in $ireland {
        &-#{unquote($town)} {
            background-image: url(#{$flag}ie.svg);
        }
    }

    @each $town in $scotland {
        &-#{unquote($town)} {
            background-image: url(#{$flag}sf.svg);
        }
    }

    @each $town in $wales {
        &-#{unquote($town)} {
            background-image: url(#{$flag}wl.svg);
        }
    }

    @each $country in $countries {
        &-#{$country} {
            background-image: url(#{$flag}#{$country}.svg);
        }
    }
}
